import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { changeCount } from "./../store/shoppingBasketSlice";
import { useUserName } from "../hooks/username";

function Cart(){
    let state = useSelector((state)=> state ) //Redux에 등록된 데이터 사용
    let dispatch = useDispatch() //Redux 등록된 데이터 변경함수 사용하기
    let username = useUserName()
    

    return(
        <div>
          <p>{username}</p>
          <Table>
            <thead>
                <tr>
                <th>#</th>
                <th>상품명</th>
                <th>수량</th>
                <th>변경하기</th>
                </tr>
            </thead>
            <tbody>
                {
                    state.shoppingBasket.map((procut , i)=>{
                      return(
                      <tr key={i}>
                        <td>{procut.id}</td>
                        <td>{procut.name}</td>
                        <td>{procut.count}</td>
                        <td><button onClick={()=>{ dispatch(changeCount(procut.id)) }}>+</button></td>
                      </tr> 
                    )}) 
                    
                }
              
            </tbody>
        </Table>   
        </div>
    )
}
export default Cart;